<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                           Product Detail History
                        </h5>
                        <button v-on:click="ClosePOPuP($event)" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="listing"> 
                            <tg-list 
                                :showCheckBox="false" 
                                :IsShowAction="false" 
                                :listType="['List']" 
                                ModuleName="Asset"
                                SubModuleCode="Asset"
                                IdentityColumn="ASSET_CATALOGUE_ID"                                                    
                                :HeaderText="$t('AssetManagement')"
                                :widgets="widgets"
                                :ListData="ProductHistoryData" 
                                :HeaderData="Headers"                       
                                :callbackfunction="GetItemHistory"                                
                                :LegendArray="LegendArray"
                                :SortExp="SortExp"
                                :SortBy="SortBy"
                                :NorecordfoundText="$t('NorecordfoundText')"
                                
                                :TotalRecords="TotalRecords"                                
                                :RenderRowActionMethod="renderActions" 
                                :ListDataCallBackFunction="GetItemHistory"
                                v-on:PagerButtonClick="pagerMethod" 
                                v-on:HeaderButtonClick="commonHeaderButtonClick">
                        </tg-list>
                        <!-- :HeaderButtons="listheaderbuttons"  -->
                            
                            <!-- <table class="table-sorting table table-bordered" id="table-openactivities-content" >
                                    <thead class="thead-bg">
                                        <tr>
                                            <th><span>EPC Tag</span></th> 
                                            <th><span>Log Date</span></th>                                          
                                            <th><span>Tag Status</span></th>
                                            <th><span>Status</span></th>                                                                                 
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,index) in ProductHistoryData" :key="index">                                            
                                            <td>{{item.EPC_TAG}}</td>
                                            <td>{{item.LOG_DATE}}</td>
                                            <td>{{item.STATUS_NAME}}</td>
                                            <td>{{item.STATUS_NAME}}</td>                                                                                 
                                        </tr>
                                        <tr v-if="noRecord">
                                            <td colspan="8" class="text-danger text-center no-record">
                                                NoRecordfound
                                            </td>
                                        </tr> 
                                    </tbody>
                                </table>  -->
                            </div>
                        </div>                        
                    </div>                   
                </div>
            </div>
        </div>
   
    
</template>
<script>
import DataService from '../../service/DataService';
export default{
    props:{
        ItemData:{
            type: String,
            required: true
        }
    },
    data(){
        return{
            noRecord:false,
            isLoading:false,
            ProductHistoryData:[],
            Headers: [
                {
                "COLUMN_NAME": "EPCTag",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("EPCTag"),
                "DISPLAY_ORDER": 1,
                },
                {
                "COLUMN_NAME": "LogDate",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("LogDate"),
                "DISPLAY_ORDER": 1,
                },
                {
                "COLUMN_NAME": "TagStatus",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("TagStatus"),
                "DISPLAY_ORDER": 1,
                },
                {
                "COLUMN_NAME": "Status",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Status"),
                "DISPLAY_ORDER": 1,
                },
            ],
            listheaderbuttons:[],
            LegendArray:[],            
            widgets:[],            
            noRecord:true,
            TotalRecords:0,
            CurrentPage: 1,
            TotalPages: 0,      
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            isPaged: 1,
            PageSize: 10,                      
        }
    },
    created:function(){               
        this.GetItemHistory();
    },
    methods:{              
        GetItemHistory:function(){ 
            this.isLoading=true;
            ;
            var url=`ePC_TAG=${this.ItemData}`;
             ;
            DataService.GetItemHistory(url)
            .then(response=> { 
                ;      
             if(response.data!=null){
                    if(response.data.length>0){
                        console.log(response.data);
                        debugger;
                        this.ProductHistoryData=response.data;
                        this.noRecord = false;
                        this.isLoading=false;
                    }
                    else{
                        this.noRecord = true;
                        this.ProductHistoryData = [];
                        this.isLoading=false;
                    }
                }
                else {
                        vm.ShowAlert(vm.$t("Unknown Error Occur"), "failure", true, vm.$t('Alert'));
                        this.isLoading=false;
                        vm.$parent.ClosePOPuP();
                }
         });
        },
        Cancel:function(item){
            this.$emit('ClosePOP',item);
        },
        ClosePOPuP:function(item){
            
            this.$emit('ClosePOPuP',item);
        },
        pagerMethod: function (value) {           
            this.pageSize = value.pageSize;
            this.PageNumber = value.PageNumber;
            this.GetItemHistory();
        },
        commonHeaderButtonClick:function(){
            
        },
        renderActions() {
            
        },

    },    
}
</script>