<template>
    <section class="main-content" id="tabs">
        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" :PageSize="PageSize"
            IdentityColumn="ASSET_CATALOGUE_ID" HeaderText="RFID Asset Listing" :ListData="RfidData" :HeaderData="Headers"
            :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NorecordfoundText')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'Image'">
                    <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                        <img class="fa fa-file text-warning action-icon" style="width:50px;"
                            src="../../assets/imagenotavailable.jpg" />
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'EPC_TAG'">
                    <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                        <em class="fa fa-file text-warning action-icon" v-on:click="ItemHistory(data.row.EPC_TAG)"></em>
                    </span>
                </template>
            </template>
        </tg-list>

        <ProductHistoryModel v-if="IsProductHistory" @ClosePOPuP="ClosePOPuP" :ItemData="ItemData"></ProductHistoryModel>
    </section>
</template>

<script>

import Schema from "../Asset/RFIDAssetListingSchema.js"
import DataService from '../../service/DataService'
import ProductHistoryModel from '../../components/Modal/ProductHistoryDetail.vue'
export default {
    components: {
        ProductHistoryModel
    },
    data() {
        return {
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            LegendArray: [],
            RfidData: [],
            LeadStatusList: [],
            StatusListForListing: [],
            Headers: [{
                "COLUMN_NAME": "LOCATION_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Location",
                "DISPLAY_ORDER": 1,
            },
            {
                "COLUMN_NAME": "sub_location",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Sub Location",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "Image",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Image",
                "DISPLAY_ORDER": 10,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "unique_name",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Name",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "ITEM_CODE",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Code",
                "DISPLAY_ORDER": 4
            },

            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "ISSUED_TO_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Assign To",
                "DISPLAY_ORDER": 6,
            },
            {
                "COLUMN_NAME": "EPC_TAG",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "RFID Tags",
                "DISPLAY_ORDER": 7,
            },
            {
                "COLUMN_NAME": "ip_address",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "IP Address",
                "DISPLAY_ORDER": 8,
            },
            {
                "COLUMN_NAME": "EPC_TAG",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Scanned Tags History",
                "DISPLAY_ORDER": 9,
                settings: {
                    isInSlot: true
                }
            },

            ],
            widgets: [],
            noRecord: true,
            TotalRecord: 0,
            CurrentPage: 1,
            TotalPages: 0,
            txtsearch: '',
            statusIds: '',
            LocationId: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            isPaged: 1,
            PageSize: 10,
            ePC_TAG: '',
            searchCondition: "",
            IsProductHistory: false,
            ItemData: '',
            userId: null,
            companyId: null,
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }

            ],
        }
    },
    async created() {
        var vm = this;
        await vm.GetPageSize();        
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.Name;
        setTimeout(() => {
            vm.FetchData();
        }, 1000);
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Location");
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Tag");
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Status");

    },
    methods: {
        async GetPageSize(){            
               var vm =this;
                var params='key=DefaultPageSize'
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {                  
                if(response.data!=null){                                    
                    vm.PageSize = parseInt(response.data);
                }                   
            });
        },
        FetchData() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var params = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${this.SortExp}&${vm.searchCondition}`;
            DataService.GetRFIDListing(params).then(response => {
                if (response.data != null) {
                    debugger
                    if (response.data.length > 0) {
                        vm.RfidData = response.data;
                        vm.TotalRecord = response.data[0].TOTAL;
                        vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.isLoading = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.RfidData = [];
                        vm.TotalRecord = 0
                        vm.isLoading = false;
                    }
                }
            });
        },
        commonHeaderButtonClick(event) {
            debugger;
            switch (event.callbackfunction) {
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            debugger
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        ItemHistory: function (item) {
            var vm = this;
            vm.IsProductHistory = true;
            vm.ItemData = item;
        },
        ClosePOPuP: function () {
            var vm = this;
            vm.IsProductHistory = false;
        },
        
        actionButtonClick() {

        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                // switch (action.title) {
                //     case 'Edit':
                //         action.isVisible = true;
                //         break;
                // }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
    }
}

</script>
<style>
.center-icon {
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>